<template>
  <div>
     <van-nav-bar title="产品列表" left-text="返回" left-arrow @click-left="onClickLeft"/>
     <section @click="goProductPage">
      <van-card
    title="赣州百花蜂蜜1斤装"
    desc="天然好蜂蜜，健康好滋味。"
    price="56.00"
    :thumb="thumbList1"/>
    </section>
    <section @click="goProductPage">
      <van-card
    title="赣州百花蜂蜜2斤装"
    desc="天然好蜂蜜，健康好滋味。"
    price="108.00"
    :thumb="thumbList2"/>
    </section>
  </div>
</template>
<script>
import { Card, NavBar } from "vant";
export default {
  components: {
    [Card.name]: Card,
    [NavBar.name]: NavBar
  },
  computed: {
    thumbList1() {
      let thumb = require("../assets/images/honey1_1.jpg");
      return thumb;
    },
    thumbList2() {
      let thumb = require("../assets/images/honey2_1.jpg");
      return thumb;
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    goProductPage(){
      this.$router.push('product')
    }
  }
};
</script>



